<template>
    <div>
        <br>
        <!-- <el-input placeholder="请输入账号" v-model="account" style="width: 200px;" clearable></el-input>
        <br>
        <el-input placeholder="请输入密码" v-model="pwd" show-password  style="width: 200px;"></el-input>
        <br>
        <el-button id='login_btn' type="primary" plain @click="onLogin">登录</el-button>
        <br>
        <el-button id='regist_btn' plain @click="onRegist">注册</el-button> -->

        <br>
        <el-form ref="form" :model="form" label-width="80px" style="width: 200px; margin: 0 auto;">
            <el-form-item label="手机号:">
                <el-input v-model="form.account">请输入手机号</el-input>
            </el-form-item>
            
            <el-form-item label="密 码:">
                <el-input v-model="form.pwd">请输入密码</el-input>
            </el-form-item>

            <el-form-item>
            <el-button type="primary" plain @click="onLogin">登录</el-button>

            <el-button type="primary" plain @click="onRegist">注册</el-button>
        </el-form-item>
    </el-form>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
    data() {
        return {
            // account:"",
            // pwd:"",

            form: {
                account: '',
                pwd: ''
            },
        };
    },

    methods: {
        onLogin() {
            //获取输入数据，调用登录接口
        console.log('onLogin!');
        this.goToLogin();
      },
      onRegist() {
        // 跳转注页面册
        console.log('onRegist!');
      },
      goToLogin:function(){
          //发送异步请求
          axios({
            method:"post",
            url:'http://localhost:8080/login',
            headers:{
                "Access-Control-Allow-Origin":"*",
                "Access-Control-Allow-Headers":"Content-Type, Content-Length, Authorization, Accept, X-Requested-With , yourHeaderFeild",
                "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS",
                "X-Powered-By": ' 3.2.1',
                "Content-Type": "application/json;charset=utf-8"
            },
            data:{
                "account":this.form.account,
                "pwd":this.form.pwd
            },
        }).then(function(response){
            console.log("okokpost!");
            console.log(response);
            if (response.data.code == 200) {
                window.open("http://www.baidu.com");
            }
        }).catch(function(error){
            console.log("error:" + error);
        });
        // axios.post("http://localhost:8080/login").then((result) => {
        //     alert("登录结果需要跳转:"+result.data.data);
        // });
      }
    },

    mounted(){}
}

</script>

